
'use strict';
import React from 'react';
import superagent from 'superagent';
import {contact_page_json} from './global';

export default class ContactPage extends React.Component{
  state = {
    modules: {
    }
  };
  constructor(){
    super();
    superagent.get(contact_page_json).set('Accept', 'application/json').end(function(err,res){
      if(!err){
        var data = JSON.parse(res.text);
        this.setState({modules:data.modules});
      }
    }.bind(this));
  }
  handleInputChange(key,e)
  {
      let data = this.state.modules;
      data[key] = e.target.value;
      this.setState({modules:data});
  }
  handleSubmit(e){
      e.preventDefault();
      superagent.post(contact_page_json).set('Content-Type', 'application/json')
      .accept('application/json').send({json:this.state.modules}).end(function(err,res){
          if(!err)
          {
              let data = JSON.parse(res.text);
              if(data.code == 0)
              {
                  alert('');
                  location.href = "/#/contact";
              }
          }
      });
  }
  render(){
    return(
      <div className="content-wrapper">
        <section className="content-header">
          <h1>
            
            <small></small>
          </h1>
          <ol className="breadcrumb">
            <li><a href="#"><i className="fa fa-dashboard"></i></a></li>
            <li className="active"></li>
          </ol>
        </section>
        <section className="content">
          <div className="row">
            <div className="col-md-12">
              <div className="box box-primary">
                <div className="box-header">
                  <i className="fa fa-edit"></i>
                  <h3 className="box-title"></h3>
                </div>
                <div className="box-body">
                  <div className="input-group margin">
                    <div className="input-group-btn">
                      <button type="button" className="btn btn-primary">:</button>
                    </div>
                    <input type="text" className="form-control" value={this.state.modules.big_title} onChange={this.handleInputChange.bind(this,'big_title')} placeholder=""/>
                  </div>
                  <div className="input-group margin">
                    <div className="input-group-btn">
                      <button type="button" className="btn btn-primary">logo:</button>
                    </div>
                    <input type="text" className="form-control" value={this.state.modules.company_logo} onChange={this.handleInputChange.bind(this,'company_logo')} placeholder="logo"/>
                  </div>
                  <div className="input-group margin">
                    <div className="input-group-btn">
                      <button type="button" className="btn btn-primary">:</button>
                    </div>
                    <input type="text" className="form-control" value={this.state.modules.company_name} onChange={this.handleInputChange.bind(this,'company_name')} placeholder=""/>
                  </div>
                  <div className="input-group margin">
                    <div className="input-group-btn">
                      <button type="button" className="btn btn-primary">:</button>
                    </div>
                    <input type="text" className="form-control" value={this.state.modules.company_website} onChange={this.handleInputChange.bind(this,'company_website')} placeholder=""/>
                  </div>
                  <div className="input-group margin">
                    <div className="input-group-btn">
                      <button type="button" className="btn btn-primary">:</button>
                    </div>
                    <input type="text" className="form-control" value={this.state.modules.company_number} onChange={this.handleInputChange.bind(this,'company_number')} placeholder=""/>
                  </div>
                  <div className="input-group margin">
                    <div className="input-group-btn">
                      <button type="button" className="btn btn-primary">:</button>
                    </div>
                    <input type="text" className="form-control" value={this.state.modules.company_address_logo} onChange={this.handleInputChange.bind(this,'company_address_logo')} placeholder=""/>
                  </div>
                  <div className="input-group margin">
                    <div className="input-group-btn">
                      <button type="button" className="btn btn-primary">:</button>
                    </div>
                    <input type="text" className="form-control" value={this.state.modules.headquarters} onChange={this.handleInputChange.bind(this,'headquarters')} placeholder=""/>
                  </div>
                  <div className="input-group margin">
                    <div className="input-group-btn">
                      <button type="button" className="btn btn-primary">:</button>
                    </div>
                    <input type="text" className="form-control" value={this.state.modules.headquarters_address_office} onChange={this.handleInputChange.bind(this,'headquarters_address_office')} placeholder=""/>
                  </div>
                  <div className="input-group margin">
                    <div className="input-group-btn">
                      <button type="button" className="btn btn-primary">:</button>
                    </div>
                    <input type="text" className="form-control" value={this.state.modules.department_name} onChange={this.handleInputChange.bind(this,'department_name')} placeholder=""/>
                  </div>
                  <div className="input-group margin">
                    <div className="input-group-btn">
                      <button type="button" className="btn btn-primary">:</button>
                    </div>
                    <input type="text" className="form-control" value={this.state.modules.company_address_number} onChange={this.handleInputChange.bind(this,'company_address_number')} placeholder=""/>
                  </div>
                  <div className="input-group margin">
                    <div className="input-group-btn">
                      <button type="button" className="btn btn-primary">:</button>
                    </div>
                    <input type="text" className="form-control" value={this.state.modules.company_address_office} onChange={this.handleInputChange.bind(this,'company_address_office')} placeholder=""/>
                  </div>
                  <div className="input-group margin">
                    <div className="input-group-btn">
                      <button type="button" className="btn btn-primary">:</button>
                    </div>
                    <input type="text" className="form-control" value={this.state.modules.email_logo} onChange={this.handleInputChange.bind(this,'email_logo')} placeholder=""/>
                  </div>
                  <div className="input-group margin">
                    <div className="input-group-btn">
                      <button type="button" className="btn btn-primary">:</button>
                    </div>
                    <input type="text" className="form-control" value={this.state.modules.email_title} onChange={this.handleInputChange.bind(this,'email_title')} placeholder="logo"/>
                  </div>
                  <div className="input-group margin">
                    <div className="input-group-btn">
                      <button type="button" className="btn btn-primary">:</button>
                    </div>
                    <input type="text" className="form-control" value={this.state.modules.email_address} onChange={this.handleInputChange.bind(this,'email_address')} placeholder=""/>
                  </div>
                  <div className="input-group margin">
                    <div className="input-group-btn">
                      <button type="button" className="btn btn-primary">:</button>
                    </div>
                    <input type="text" className="form-control" value={this.state.modules.phone_logo} onChange={this.handleInputChange.bind(this,'phone_logo')} placeholder=""/>
                  </div>
                  <div className="input-group margin">
                    <div className="input-group-btn">
                      <button type="button" className="btn btn-primary">:</button>
                    </div>
                    <input type="text" className="form-control" value={this.state.modules.headquarters_title} onChange={this.handleInputChange.bind(this,'headquarters_title')} placeholder=""/>
                  </div>
                  <div className="input-group margin">
                    <div className="input-group-btn">
                      <button type="button" className="btn btn-primary">:</button>
                    </div>
                    <input type="text" className="form-control" value={this.state.modules.headquarters_phone} onChange={this.handleInputChange.bind(this,'headquarters_phone')} placeholder=""/>
                  </div>
                  <div className="input-group margin">
                    <div className="input-group-btn">
                      <button type="button" className="btn btn-primary">:</button>
                    </div>
                    <input type="text" className="form-control" value={this.state.modules.company_title} onChange={this.handleInputChange.bind(this,'company_title')} placeholder=""/>
                  </div>
                  <div className="input-group margin">
                    <div className="input-group-btn">
                      <button type="button" className="btn btn-primary">:</button>
                    </div>
                    <input type="text" className="form-control" value={this.state.modules.company_phone} onChange={this.handleInputChange.bind(this,'company_phone')} placeholder=""/>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="row btn-div">
            <button type="button" className="btn btn-block btn-primary btn-lg" onClick={this.handleSubmit.bind(this)}></button>
          </div>
        </section>
      </div>

    )
  }

}



